@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
html{color:RGB(95, 92, 88);box-sizing:border-box}body{margin:0;font-family:"Open Sans",sans-serif;font-weight:400;font-size:14px;color:RGB(95, 92, 88);background-color:#eeecec;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.wrapper{display:grid;grid-gap:8px;gap:8px;max-width:888px;min-height:auto;justify-content:center;align-items:center;margin:0 auto;padding:40px}.top-container{display:grid;max-width:808px;min-height:375px;justify-content:center;align-items:center}.top-container .info-card{background-color:white;padding:20px;display:flex;box-shadow:0 1px 4px rgba(150,150,150,0.7);border-radius:3px}.bottom-container{display:grid;grid-gap:8px;gap:8px;grid-template-columns:repeat(2, 1fr);max-width:808px}.bottom-container .movies{background-color:white;display:grid;align-items:flex-start;box-shadow:0 1px 4px rgba(150,150,150,0.7);border-radius:3px}@media only screen and (min-width: 320px) and (max-width: 599px){body{line-height:1.8rem}.wrapper{height:-webkit-max-content;height:max-content;padding:20px}.bottom-container{grid-template-columns:1fr}}.graph-container{width:100%}.section-header{display:flex;justify-content:space-between}.graph-text{margin:0;color:RGB(95, 92, 88)}.title{font-weight:600;font-size:20px;color:RGB(48, 46, 44)}#sub-title{margin-top:6px;color:RGB(95, 92, 88)}.right-align{text-align:end}.stats-row{display:grid;margin:30px 0 24px 0;height:189px;grid-gap:22.5px;gap:22.5px;grid-template-areas:'line-chart line-chart stat-1' 'line-chart line-chart stat-2'}.stats-row .graph{grid-area:line-chart;max-height:250px;width:100%;margin:auto 0}#analysis{font-size:14px;font-weight:600;margin:0}#analysis-text{font-size:14px;font-weight:400;margin:0;padding:0}#no-space{margin:0;padding:0}#update-text{display:flex;justify-content:flex-end;font-size:12px;min-width:180px}#update-div{margin-top:5px}.clock{height:12px;-webkit-transform:translate(-2px, 2.5px);transform:translate(-2px, 2.5px)}.stat-one{grid-area:stat-1;min-width:242px;margin-top:5px}.stat-two{margin-top:25px;grid-area:stat-2}.stat-number{font-size:24px;color:RGB(48, 46, 44);margin:0;padding-bottom:0}.stat-identifier{margin-top:2px;font-size:12px;font-weight:400;margin-bottom:8px}@media only screen and (min-width: 320px) and (max-width: 599px){.right-align{text-align:start}.section-header{display:grid;height:-webkit-max-content;height:max-content}.graph-text{margin:0}.stats-row{grid-template-areas:'line-chart' 'stat-1' 'stat-2'}#no-space{margin-top:155px}#update-text{justify-content:flex-start}.clock{-webkit-transform:translate(0, 7.5px);transform:translate(0, 7.5px);margin-right:.25rem}}canvas{max-width:100%;min-height:189px}@media only screen and (min-width: 320px) and (max-width: 599px){canvas{min-height:138px;max-height:140px}}@media only screen and (min-width: 1280px) and (max-width: 1920px){canvas{min-width:495.5px;min-height:189px}}.pb-unfilled{height:6px;width:100%;background-color:RGB(238, 236, 236);border-radius:2px}.movie-container{margin:20px 20px 0 20px}#release-date{display:grid;align-items:center;font-weight:600;font-size:14px;margin-top:6px}#release-date p{color:RGB(48, 46, 44);margin-top:6px}.po-text{margin:0;color:RGB(48, 46, 44)}.po-text #puchaseDate{font-weight:400;font-size:12px}.po-title{font-weight:600;font-size:20px;color:RGB(48, 46, 44)}.clock-2{height:11px;-webkit-transform:translate(0, 1px);transform:translate(0, 1px)}.flex-row{display:flex;justify-content:space-between}.flex-row .movie-date{font-size:14px;font-weight:600}.buy-now{padding:16px 0;align-items:center;height:36px}#bottom-grey{border-bottom:1px solid RGB(223, 222, 222)}.btn{display:grid;justify-content:center;align-items:center;background-color:#3a8ea5;text-decoration:none;color:white;border-radius:3px;height:36px;width:109px;font-weight:600;font-size:14px}@media only screen and (min-width: 320px) and (max-width: 599px){.movie-container{margin:20px}.po-text{margin:0}.flex-row{display:flex;height:-webkit-max-content;height:max-content}}

